import React, { Component } from 'react';
import { compose } from 'react-apollo';
import AdminView from '../components/AdminView';
import Loader from '../../common/components/global/components/Loader';
import '../styles/productspecs.scss';
import ProductSpecs from '../components/ProductSpecs';
import { ADMIN_PRODUCTS_QUERY /*MODIFY_PRODUCT_DATA*/ } from '../../common/schema';

const columns = [
  {
    title: (
      <a href="#">
        {/* {t('users.column.name')} {this.renderOrderByArrow('username')} */}
        Name
      </a>
    ),
    dataIndex: 'node',
    key: 'title',
    // eslint-disable-next-line
    render: (text, record) => {
      // console.log('TEXT::', text);
      // console.log('RECORD:', record);
      return (
        <div>
          {text}
          {/* <Link to={{ pathname: `/admin/locations/${record.node.handle}` }}>{text}</Link> */}
        </div>
      )
    }
  }
];
class Products extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { productsLoading, sfy_shop } = this.props;
    console.log('The PRODUCT PROPS::::', sfy_shop);

    if (productsLoading) {
      return (
        <AdminView {...this.props}>
          <Loader />
        </AdminView>
      );
    }
    return (
      <AdminView {...this.props}>
        <div className="admin__productspecs">
          <ProductSpecs dataSource={sfy_shop.products.edges} columns={columns} />
        </div>
      </AdminView>
    );
  }
}
const ProductsApollo = compose(ADMIN_PRODUCTS_QUERY(null, 50))(Products);

export default ProductsApollo;
